import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { SharedService } from '../../shared/services/shared.service';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-item-detail',
  templateUrl: './item-detail.component.html',
  styleUrls: ['./item-detail.component.css']
})
export class ItemDetailComponent implements OnInit {

    _host             = environment.config.BASE_URL;

	object      = {
        scheduler:[{startTime:'',closeTime:''}]
    };
	products    = [];
    reviews     = [];
    name        = '';
    sortBy      = '';
    pageReview  = 0;
    totalReview = 0;
	constructor(
		private _route: ActivatedRoute,
    	private _sharedService: SharedService) { }

    ngOnInit() {
  		this.fetch();
        this.favorate();
        this.fetchProduct();
        this.fetchReviews();
    }
  
    fetch() {
    	let options = {
    		id:this._route.snapshot.params['id']
    	}
        this._sharedService.queryParams('itemDetail',options).then((res: any) => {
            if(res.success) {
                this.object = res.data.data[0]; 
                // this.products = res.data.key;
            } else {
            }
        },(error) => { });
    }


    favorate() {
            let options = {
               id:this._route.snapshot.params['id'],
               count:10,
               page:1
            }
            this._sharedService.queryParams('itemFavorite',options).then((res: any) => {
                if(res.success) {
                    // this.object = res.data.item[0]; 
                    // this.products = res.data.key;
                } else {
                }
            },(error) => { });
    }

    fetchProduct() {
        let options = {
               id:this._route.snapshot.params['id'],
               count:1000,
               page:1,
               sortBy:this.sortBy,
               name:this.name
        }
        this._sharedService.loader('show');
        this._sharedService.queryParams('ItemReviewsWithProduct',options).then((res: any) => {
            this._sharedService.loader('hide');
            if(res.success) {
                this.products = res.data.data;
            } else {
            }
        },(error) => { });
    }

    fetchReviews() {
        this.pageReview+=1;
        let options = {
               id:this._route.snapshot.params['id'],
               count:2,
               page:this.pageReview
        }
        this._sharedService.loader('show');
        this._sharedService.queryParams('itemreviews',options).then((res: any) => {
            this._sharedService.loader('hide');
            if(res.success) {
                res.data.products.forEach((obj)=>{
                    this.reviews.push(obj);
                    this.totalReview = res.data.total;
                })
            } else {
            }
        },(error) => { });
    }

    rating(rating) {
        if(rating) return parseInt(rating);
        else return 0;
    }

}



